import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const routes = [
  {
    path: '*',
    redirect: '/home'
  },
  {
    name: 'user',
    component: () => import('./view/user'),
    meta: {
      title: '我的',
    }
  },
  {
    name: 'home',
    component: () => import('./view/home'),
    meta: {
      title: '首页'
    }
  },
  {
    name: 'goods',
    component: () => import('./view/goods'),
    meta: {
      title: '商品详情'
    }
  },
  {
    name: 'customise',
    component: () => import('./view/customise'),
    meta: {
      title: '定制中心'
    }
  },
  {
    name: 'send',
    component: () => import('./view/send'),
    meta: {
      title: 'send'
    }
  },
  {
    name: 'information',
    component: () => import('./view/information'),
    meta: {
      title: 'information'
    }
  },
  {
    name: 'mine',
    component: () => import('./view/mine'),
    meta: {
      title: 'mine'
    }
  },
  {
    name: 'history',
    component: () => import('./view/history'),
    meta: {
      title: 'history'
    }
  },
  {
    name: 'infomanage',
    component: () => import('./view/infomanage'),
    meta: {
      title: 'infomanage'
    }
  },
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  document.title = '大陆集团康迪泰克';
  let isWeiXin = () => { return navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1 }
  if (isWeiXin()) {
    next();
  } else {
    alert('请用微信浏览器打开！')
    return false
  }
});

export {
  router
};
