import Vue from 'vue';
import Vuex from 'vuex'
import axios from 'axios';
import QS from 'qs'
import App from './App';
import { router } from './router';
import store from './store'

import '@/style/index.scss' // global css

Vue.prototype.$axios = axios;
Vue.prototype.qs = QS;

import { Dialog } from 'vant';
Vue.use(Vuex)
Vue.use(Dialog);

new Vue({
  router,
  store,
  el: '#app',
  render: h => h(App)
});
