const state = {
  products: [],
  result: [],
  token: '',
  user: {
    companyName: '',
    contactPerson: '',
    email: '',
    handbookName: ''
  }

}

const mutations = {
  SET_PRODUCTS: (state, products) => {
    state.products = products;
  },
  SET_USER: (state, user) => {
    state.user = user;
  },
  SET_RESULT: (state, result) => {
    state.result = result;
  },
  SET_TOKEN: (state, result) => {
    state.token = result;
  }
}

const actions = {
  setProducts({ commit }, products) {
    commit('SET_PRODUCTS', products);
  },
  setUser({ commit }, user) {
    commit('SET_USER', user);
  },
  setResult({ commit }, result) {
    commit('SET_RESULT', result);
  },
  setToken({ commit }, result) {
    commit('SET_TOKEN', result);
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
